@use "./PhotosnapStyles/base" as Photosnap;

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

h1,
p {
  margin: 0;
}

a {
  text-decoration: none;
  cursor: pointer;
  &:hover {
    text-decoration: none;
  }
}

ul,
li {
  text-decoration: none;
  list-style: none;
  margin: 0;
  padding: 0;
}

button {
  border: none;
}
